import React, { Component } from 'react';
import{Route, HashRouter} from "react-router-dom"
import Home from "./home.js"
import './App.css';

class App extends Component {
  render() {
    return (
        <HashRouter>
        <Route exact path="/" render={(props) => <Home />} />
        </HashRouter>
    );
  }
}

export default App;
