import React, { Component } from 'react';
import start_icon from "./static/img/play.svg"
import check_icon from "./static/img/check_blank.svg"
import checked_icon from "./static/img/check_full.svg"
import pause_icon from "./static/img/pause.svg"

export default class Task extends Component{
  render(){
    const active = this.props.started
    const complete = this.props.done

    return (
        <div className="oneTask">
            <img className="leftButton" src={((active) ? pause_icon : start_icon )} alt={active ? "Pause" : "Start"} onClick={this.props.onStart} />
            {complete  ? (<span><s> {this.props.name} </s></span>) : (<span>{this.props.name}</span>)}
            <img className="rightButton" src={((complete) ? checked_icon : check_icon )} alt={complete ? "Mark not done" : "Mark done"} onClick={this.props.onComplete} />
        </div>
    );
  }

}
