import React, { Component } from 'react'
import TextInput from "./text.js"
import Task from "./task.js"
import Person from "./person.js"
import Tag from "./tag.js"
import AddFriend from "./addFriend.js"
import {SortableContainer, SortableElement, arrayMove} from 'react-sortable-hoc';
import axios from 'axios'
import start_icon from "./static/img/play.svg"
import add_icon from "./static/img/plus.svg"

const SortableItem = SortableElement(({value}) => <li>{value}</li>);

const SortableList = SortableContainer(({items}) => {
  return (
    <ul>
      {items.map((value, index) => (
        <SortableItem key={`item-${index}`} index={index} value={value} />
      ))}
    </ul>
  );
});

export default class Home extends Component {
    constructor(props){
        super(props);
        this.state = {
            showing: "tasks", //tasks, tags, guess
            current_tag: "",
            possible_tags: [],
            guesses: [],
            tasks: [],
            friends: [],
            latest_type: "tag", //tag, user, date
            new_task: {
                name: "",
                started: false,
                times: [],
                done: [],
                tags: [],
                aim_date: "",
                due_date: "",
                users: []
            },
            text: "",
            user: {
                name: "Ilya",
                id: "0"
            }
        }
        this.getTasks = this.getTasks.bind(this);
        this.getTags = this.getTags.bind(this);
        this.processText = this.processText.bind(this);
        this.commitAndClear = this.commitAndClear.bind(this);
        this.commitTask = this.commitTask.bind(this);
        this.textChanged = this.textChanged.bind(this);
        this.complete = this.complete.bind(this);
        this.start = this.start.bind(this);
        this.onSortEnd = this.onSortEnd.bind(this);
        this.addTask = this.addTask.bind(this);
        this.show = this.show.bind(this);
        this.sync = this.sync.bind(this);
        this.reorder = this.reorder.bind(this);
        this.componentDidMount = this.componentDidMount.bind(this);
    }
    show(type){
        this.setState({showing: type});
    }
    componentDidMount(){
        this.getTasks();
        this.getTags();
    }

    sync(){
        this.reorder();
        this.getTasks();
        this.getTags();
    }

    getTasks(){
        axios.get('https://api.ibreznik.com/now', {params: {tag: this.state.current_tag}}).then(response => response.data)
          .then((data) => {data.sort((a, b) => a.block_order - b.block_order).reverse();
        this.setState({tasks: data});
        });
    }
    componentWillUnmount(){
      this.reorder();
    }
    reorder(){
        const tasks = this.state.tasks.slice();
        const length = tasks.length
        for(var i = 0; i < length; i++){
            tasks[i].block_order = length - i;
            this.update(tasks[i]);
        } 
    }
    update(tsk){
      axios.put('https://api.ibreznik.com/task', tsk)
    }
    getTags(){
        axios.get('https://api.ibreznik.com/tags').then(response => (this.setState({possible_tags : response.data})))
    }
    addFriend(email){
    }
    processText(){
        var text = this.state.text;
        var tags = text.split(' ').filter(v=> v.startsWith('#'))
        for(var i = 0; i<tags.length; i++){
            text = text.replace(tags[i],"");
            tags[i] = tags[i].replace("#","").toLowerCase();
        }
        text = text.trim();
        this.setState({text: text});
//        var newTsk = this.state.newTask;
//        newTsk.tags = tags;
//        this.setState({newTask: newTsk});
        return ({
            "text":text,
            "tags":tags
        })  
    }
    commitAndClear(task){
        this.commitTask(task);
        this.setState({text: ""});
    }
    commitTask(tsk){
        console.log(tsk);
        axios.post('https://api.ibreznik.com/task', tsk)
            .then(res => {
            var all = this.state.tasks;
            const i = all.findIndex(item=>item._id === undefined);
            all[i]._id = {$oid:res.data}
            this.setState({tasks:all});
            });
    }
    addTask(strt = false){
        const times = strt ? [(new Date().getTime())] : []
        const payload = this.processText();
        const task = { 
            "name": payload.text,
            "tags": payload.tags,
            "times":times,
            "block_order":0,
            "started":strt,
            "done":false
        };
        var all = this.state.tasks;
        all.push(task);
        this.setState({tasks:all});
        this.commitAndClear(task);
    }
    textChanged(newText){
        this.setState({text:newText});
    }
    createTag(tag){
    }
    setCurrentPerson(index){
    }
    setCurrentTag(tag){
        this.setState({current_tag:tag}, () => {
            this.getTasks(); this.setState({showing: "tasks"});
        });
        console.log(this.state.tasks);
    }
    addTagToCurrent(index){
    }
    complete(i){
        var tasks = this.state.tasks.slice();
        tasks[i].done = !tasks[i].done;
        if(tasks[i].started){
            tasks[i].started = !tasks[i].started;
            tasks[i].times.push(new Date().getTime());
        }   
        this.setState({tasks:tasks});
        this.update(tasks[i]);
    }
    start(i){
        const tasks = this.state.tasks.slice();
        tasks[i].started = !tasks[i].started;
        tasks[i].times.push(new Date().getTime());
        if(tasks[i].done){
            tasks[i].done = false;
        }   
        this.setState({tasks:tasks});
        this.update(tasks[i]);
    }
    getLatestTag(){
    }
    onSortEnd = ({oldIndex, newIndex}) => {
        this.setState({
            tasks: arrayMove(this.state.tasks, oldIndex, newIndex),
        });
    }

    render(){
        const showing = this.state.showing;
        const latest_type = this.state.latest_type;
        const tag = this.getLatestTag();
        var show = "";
        if(showing === "tasks"){
            show = this.state.tasks.map((tsk, index) => 
                (<Task 
                    name={tsk.name} 
                    id={tsk._id} 
                    started={tsk.started} 
                    done={tsk.done} 
                    onStart={() => this.start(index)}
                    onComplete={() => this.complete(index)}
                />));
        } else if(showing === "guess"){
            show = this.state.possible_tags.map((tg, index) => 
                (<Tag 
                    name={tg} 
                    onClick={() => this.addTagToCurrent(index)}
                />));
        } else if(showing === "tags"){
            console.log(this.state.possible_tags);
            if(latest_type === "tag"){
                show = this.state.possible_tags.map((tg, index) => 
                (<Tag 
                    name={tg} 
                    onClick={() => this.setCurrentTag(tg)}
                />));
            } else if (latest_type === "user"){
                show = this.state.friends.map((tg, index) => 
                (<Person 
                    name={tg} 
                    onClick={() => this.setCurrentPerson(index)}
                />));
                
            }
        }

        var control = "";
        if(showing === "guess"){
            if(latest_type === "tag" ){
                control = (<div className="createNewTag" onClick={() => this.createTag(tag)}>"Create new tag "+tag</div>)
            } else if(latest_type === "user"){
                control =(<AddFriend onDone={this.addFriend} />)
            }
        } else{
            control = (<div className="controls">
                    <div className= "rightControl">Edit</div>
                    <div className= "leftControl" onClick={this.sync} >Sync</div>
                    <center>
                {(showing === "tasks") ?
                           (<div className= "centerControl" onClick={() => this.show("tags")}> Switch to ...</div>) :
                           (<div className= "centerControl" onClick={() => this.setCurrentTag("")}> Show all</div>)
                        }
                    </center>
                </div>)}
        return(
            <div className="main">
                <div className="top">
                    <img className="add"
                        src = {add_icon}
                        alt = "Add"
                        height = "35"
                        onClick={() => this.addTask(false)}
                    />
                    <img className="addAndStart"
                        src = {start_icon}
                        alt = "Start"
                        height = "35"
                        onClick={() => this.addTask(true)}
                    />
                    <center><TextInput 
                        text={this.state.text} 
                        onChange={this.textChanged}
                    /></center>
                </div>
                {control}
                <div className="body">
                    <SortableList items= {show} distance={ 2 } onSortEnd={this.onSortEnd} />
                </div>
            </div>
        );
    }
}
